* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-size: 15px;
	font-family: 'Roboto', sans-serif;
}

nav {
	margin: 0;
	position: relative;
	width: 100%;
	height: 3.5rem;
	display: flex;
	background-color: #63a4ff;
	background-image: linear-gradient(315deg, #63a4ff 0%, #83eaf1 74%);
}

span#icons {
	width: 50%;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-left: 0.8rem;
}

@media only screen and (min-width: 30rem) {
	span#icons {
		width: 30%;
	}
}

.nav-logo {
	display: block;
	border-radius: 50%;
	max-height: 50px;
	max-width: 50px;
	margin: 0.3rem;
}

.fas.fa-bars {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	width: 50%;
	justify-self: flex-end;
	margin-right: 1rem;
	font-size: 1.5rem;
	z-index: 999;
}

@media only screen and (min-width: 30rem) {
	.fas.fa-bars {
		display: none;
	}
}

.nav-list {
	display: flex;
	position: absolute;
	top: -100%;
	margin-right: 0.5rem;
	width: 70%;
	list-style-type: none;
	transition: all 300ms;
	transform: scale(0);
}

.nav-list.active {
	transform: scale(1);
	top: 100%;
	flex-direction: column;
	width: 100vw;
	z-index: 999;
	background-color: #63a4ff;
	background-image: linear-gradient(315deg, #63a4ff 0%, #83eaf1 74%);
	transition: all 300ms;
}

.nav-list.active li {
	margin-bottom: 1rem;
}

@media only screen and (min-width: 30rem) {
	.nav-list,
	.nav-list.active {
		position: static;
		transform: scale(1);
		flex-direction: row;
		justify-content: flex-end;
	}

	.nav-list.active li {
		margin-bottom: 0;
	}

	.nav-list li:hover {
		border-bottom: 0.4rem solid #e27d60;
		background-color: transparent !important;
		background-image: none !important;
	}
}

.nav-list li {
	padding: 0 0.8rem;
	margin-bottom: 1rem;
	height: 100%;
	display: flex;
	transition: all 200ms;
}

.nav-list li:hover {
	background-color: #63a4ff;
	background-image: linear-gradient(315deg, #83eaf1 74%, #63a4ff 0%);
	transition: all 240ms;
}

.nav-link {
	height: 100%;
	width: 100%;
	text-decoration: none;
	color: black;
	font-size: 1.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
}

#company {
	position: relative;
	width: 100%;
}

@media only screen and (min-width: 30rem) {
	#company {
		max-width: 10rem;
	}
}

#company-button {
	cursor: pointer;
	height: 100%;
	width: 100%;
	background-color: transparent;
	border: none;
	outline: none;
	align-items: center;
	justify-content: center;
	display: flex;
}

.fa.fa-angle-down,
#company-button span {
	font-size: 1.5rem;
}

.fa.fa-angle-down {
	margin-left: 0.5rem;
	transition: all 240ms;
}

.fa.fa-angle-down.rotate {
	transform: rotateZ(180deg);
}

.company-list {
	padding-top: 1rem;
	list-style-type: none;
	position: absolute;
	top: 100%;
	z-index: 1000;
	transform: scale(0);
	display: flex;
	flex-direction: column;
	width: 100%;
	transition: all 240ms;
}

.company-list.active {
	transform: scale(1);
	background-color: #63a4ff;
	background-image: linear-gradient(315deg, #63a4ff 0%, #83eaf1 74%);
}

.company-list li {
	margin-bottom: 1rem !important;
}

.company-list li:hover {
	background-color: #63a4ff !important;
	background-image: linear-gradient(
		315deg,
		#83eaf1 74%,
		#63a4ff 0%
	) !important;
	border: none;
}

footer {
	padding: 1rem;
	margin: 1rem 0 0 0;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	background-color: black;
	color: white;
}

#connections {
	width: 60%;
	display: flex;
	flex-wrap: wrap;
	padding: 0.5rem;
	justify-content: flex-end;
	align-items: center;
}

#logo {
	width: 40%;
	padding: 0.5rem;
	text-align: left;
}

#logo h2 {
	font-size: 1.4rem;
}

#logo h4 {
	font-weight: normal;
	font-size: 1rem;
}

#connections a {
	text-decoration: none;
	color: white;
	display: block;
	margin: 0.8rem;
}

#connections a i {
	font-size: 1.5rem;
}

.account {
	text-decoration: none;
	color: black;
	display: block;
	margin: 1rem;
}

.account-card {
	margin: 5px;
	border-radius: 10%;
	box-shadow: 0 0 5px white;
	line-height: 1.5rem;
	cursor: pointer;
	background-color: white;
	padding: 0.6rem;
	max-width: 300px;
	min-width: 100px;
	display: grid;
	grid-template-areas:
		'name name name avatar'
		'company company company company'
		'branch branch branch branch';
	grid-gap: 5px;
	align-items: center;
	justify-content: center;

	transition-timing-function: ease-in-out;
	transition-duration: 0.3s;
}

.account-card:hover {
	transform: scale(1.1);
}

.account-card-name {
	grid-area: name;
}

.account-card-company {
	grid-area: company;
}

.account-card-branch {
	grid-area: branch;
}

.account-card-avatar {
	grid-area: avatar;
	height: 50px;
	width: 50px;
	border-radius: 50%;
}

#accounts {
	margin: 0.6rem;
	display: flex;
	flex-flow: wrap;
	justify-content: space-around;
	transform: skewY(2deg);
}

.buttonElement {
	margin-top: 1rem;
	display: flex;
	justify-content: space-around;
}

div.buttonElement .button {
	font-family: 'Roboto Slab', serif;
	color: white;
	font-size: 1.2rem;
	border: none;
	background-color: #00c5cd;
	outline: none;
	width: 100px;
	border-radius: 5px;
	padding: 0.4rem;
	transition: 480ms;
}

div.buttonElement .button:hover {
	background-color: #5bdfeb;
	transition: 480ms;
}

.formElement {
	align-items: center;
	margin-bottom: 1rem;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: repeat(2, 1fr);
	grid-gap: 0.5rem;
}

@media only screen and (min-width: 380px) {
	.formElement {
		grid-template-columns: 30% 1fr;
		grid-template-rows: 1fr;
		height: 30px;
	}
}

.formElement label {
	font-family: 'Roboto Slab', serif;
	font-size: 1.2rem;
}

.formElement input {
	padding: 0.4rem;
}

form {
	font-family: 'Roboto Slab', serif;
	border: 3px solid #00c5cd;
	max-width: 450px;
	margin: 0.6rem;
	padding: 1rem;
	background-color: white;
	background-image: url(/blogwebsite/static/media/wood.60e62ce3.jpg);
	text-align: center;
}

#form-title {
	font-family: 'Roboto Slab', serif;
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

#hero {
	display: grid;
	grid-template-columns: repeat(1, minmax(100px, 1fr));
	grid-template-rows: repeat(1, 1fr);
	grid-gap: 1.5rem;
	margin-bottom: 3rem;
}

@media only screen and (min-width: 700px) {
	#hero {
		grid-template-columns: repeat(2, minmax(100px, 1fr));
		grid-template-rows: repeat(1, 1fr);
		grid-row-gap: 1.5rem;
	}
}

#hero-text {
	margin-top: auto;
	margin-bottom: auto;
}

#description {
	width: 100%;
	text-align: left;
	padding: 1rem;
	width: auto;
	font-size: 1.4rem;
	color: #333;
	font-weight: 500;
	font-family: Arial;
	line-height: 2rem;
}

#description-title {
	width: 100%;
	line-height: 2rem;
	font-size: 1.8rem;
	font-weight: 600;
}

@media only screen and (max-width: 300px) {
	#hero div svg {
		display: none;
	}
}

#head {
	-webkit-animation: headshake 2s ease infinite alternate;
	        animation: headshake 2s ease infinite alternate;
	transform-origin: center;
	transform-box: fill-box;
}

@-webkit-keyframes headshake {
	from {
		transform: rotateZ(-12deg);
	}
	to {
		transform: rotateZ(12deg);
	}
}

@keyframes headshake {
	from {
		transform: rotateZ(-12deg);
	}
	to {
		transform: rotateZ(12deg);
	}
}

#Ball1,
#Ball2,
#Ball3 {
	-webkit-animation: grow 1s ease infinite alternate;
	        animation: grow 1s ease infinite alternate;
	transform-origin: center;
	transform-box: fill-box;
}

#Ball1 {
	-webkit-animation-delay: 1s;
	        animation-delay: 1s;
}

#Ball2 {
	-webkit-animation-delay: 0.5s;
	        animation-delay: 0.5s;
}

#Ball3 {
	-webkit-animation-delay: 0;
	        animation-delay: 0;
}

@-webkit-keyframes grow {
	from {
		transform: scale(1);
	}
	to {
		transform: scale(1.2);
	}
}

@keyframes grow {
	from {
		transform: scale(1);
	}
	to {
		transform: scale(1.2);
	}
}

#accounts-section {
	background-color: #63a4ff;
	background-image: linear-gradient(315deg, #83eaf1 50%, #63a4ff 50%);
	margin-bottom: 2rem;
	padding: 1rem;
	text-align: center;
	overflow: visible;
}

#accounts-section h3.title {
	font-size: 2rem;
	color: black;
}

@media only screen and (min-width: 625px) {
}

#signup {
	display: flex;
	justify-content: space-around;

	margin-bottom: 2rem;
}

#logInContainer {
	display: flex;
	justify-content: space-around;
	align-items: center;
	min-height: 50vh;
}

.App {
	text-align: center;
}

header {
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	margin: 0;
	width: 100%;
	height: 5rem;
}

main {
	margin-top: 5.5rem;
}

